<template>
  <div class="statis-container">
    <div class="header">
      <div class="inner-wrapper">
        <div class="header-area" @click="navToList()">
          <i class="el-icon-back mr10"></i>
          <!-- <span>问卷标题</span> -->
        </div>
        <div class="head-nav">
          {{survey.title}}
        </div>
        <div class="user">
          <span class="username">{{name}}</span>
          <i class="el-icon-switch-button" @click="logout"></i>
        </div>
      </div>
    </div>
    <div class="content">
      <div style="display:flex; justify-content: flex-end">
        <a style="cursor:pointer" :href="`/surveyapi/b/statis/export?sid=${$route.params.sid}`" target="_blank">导出Excel</a>
      </div>
      <div style="margin-top: 15px;" v-for="(item, index) in questions" :key="index">
        <div class="ansimg">
            <div class="anshead">
                <span class="title">
                    {{'Q' + (index + 1) + ': ' + item.subject}}
                </span>
            </div>
            <div class="showimg">
                <div class="chartdiv" v-if="/^[1|2|4|5]/.test(item.qtype)">
                    <div class="chart_table">
                      <el-table
                        :data="item.statis"
                        max-height="380"
                        style="width: 100%">
                        <el-table-column
                          prop="o_content"
                          label="选项"
                          width="205">
                        </el-table-column>
                        <el-table-column
                          prop="count"
                          label="回答人数">
                        </el-table-column>
                      </el-table>
                    </div>
                    <div class="charts-container" style="width: 50%">
                      <ve-pie :data="{columns: ['o_content', 'count'], rows: item.statis}"></ve-pie>
                    </div>
                </div>

                <div class="contentdiv" style="margin-bottom: 30px" v-if="/^3/.test(item.qtype)">
                  <div style="text-align:left; margin-bottom: 30px">{{'回答人数：' + item.rplcount}}</div>
                  <div>答题情况</div>
                  <div style="text-align:left; margin-bottom: 40px; padding: 0 20px;">
                    <div v-for="answer in item.answers" :key="answer" class="answer">
                      {{answer}}
                    </div>
                  </div>
                  <el-pagination
                    class="pager"
                    background
                    layout="prev, pager, next"
                    :hide-on-single-page="item.totalPage <= 1"
                    :current-page.sync="item.currentPage"
                    @current-change="getList(item, index)"
                    :page-count="item.totalPage">
                  </el-pagination>
                </div>

                <div v-if="/^6/.test(item.qtype)">
                  <div v-for="(row, rIndex) in item.statis" :key="rIndex">
                    <div class="chart-title">
                      <div>“{{row[0].r_content}}”答题情况：</div>
                    </div>
                    <div class="chartdiv">
                      <div class="chart_table">
                        <el-table
                          :data="row"
                          max-height="380"
                          style="width: 100%">
                          <el-table-column
                            prop="o_content"
                            label="选项"
                            width="205">
                          </el-table-column>
                          <el-table-column
                            prop="count"
                            label="回答人数">
                          </el-table-column>
                        </el-table>
                      </div>
                      <div class="charts-container" style="width: 50%">
                        <ve-pie :data="{columns: ['o_content', 'count'], rows: row}"></ve-pie>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
        </div>
    </div>
    </div>
  </div>
</template>

<script>
  import api from '~/api/api';
  import {mapState} from 'vuex';
  import store from '~/store/index';
  import * as types from '~/store/mutation-type';
  import {getStore} from '~/tools/utils';
  import VePie from 'v-charts/lib/pie.common'

  export default {
    data() {
      return {
        name: getStore('name'),
        questions: null,
        survey: {title: ''},
        pagesize: 10
      };
    },
    mounted() {
      this.init();
    },
    components: { VePie },
    methods: {
      getList(item, index) {
        api.getStatisDetail(this.$route.params.sid, item.qid, item.currentPage, this.pagesize).then(res => {
          if(!res) return;
          item.answers = res.data.map(x => x.content);
          item.totalPage = res.pagecount;
        });
      },
      init() {
        let questions = [];
        api.getStatisInfo(this.$route.params.sid).then(res => {
          if(!res) return;
          let promiseList = [];
          questions = res.question;
          questions.forEach(item => {
            if(/^3/.test(item.qtype)) {
              promiseList.push(api.getStatisDetail(this.$route.params.sid, item.qid, 1, this.pagesize));
            } else {
              promiseList.push(Promise.resolve());
            }
          });
          this.survey = Object.assign({}, res.survey);
          return Promise.all(promiseList);
        }).then(res => {
          questions = questions.map((item, index) => {
            if(/^3/.test(item.qtype)) {
              item.answers = res[index].data.map(x => x.content);
              item.currentPage = 1;
              item.totalPage = res[index].pagecount
            }
            return item;
          });
          this.questions = questions;
        });
      },
      logout() {
        api.logout().then(res => {
          this.$router.push({ path: `/login` });
        })
      },
      navToList() {
        store.commit(types.CLEAR_SELECTED_ITEM);
        this.$router.push({ path: '/list/my' });
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .statis-container {
    min-height: 100%;
    width: 100%;
    background #EEEFF1
    overflow: hidden;
    .header {
      position: fixed;
      -webkit-box-shadow: 0 2px 4px 0 rgba(0,0,0,.08);
      box-shadow: 0 2px 4px 0 rgba(0,0,0,.08);
      background: #fff;
      font-size: 16px;
      -webkit-transition: all .4s ease;
      -o-transition: all .4s ease;
      transition: all .4s ease;
      height: 64px;
      display flex
      width 100%
      z-index 3
      .inner-wrapper {
        min-width 1306px
        width 100%
        margin 0 auto
        position relative
        .header-area {
          position absolute
          cursor pointer
          left 40px
          padding-top 22px
          .mr10 {
            margin-right 10px
          }
        }
        .head-nav {
          padding-top: 22px;
          position absolute
          left 50%
          transform translateX(-50%)
        }
        .user {
          position absolute
          right 40px
          top 22px
          .username {
            margin-right 10px
          }
          i {
            &:hover {
              color red
            }
          }
        }
      }
    }
    .content {
      margin: 79px auto 0;
      width: 930px;
      min-height: 450px;
      margin-bottom: 20px;
      font-size 14px;
      .ansimg {
        border: 1px #dbdbdb solid;
        background-color: #fff;
        margin-top: 15px;
        border-radius: 8px;
        .anshead {
          background-color: #FAFAFA;
          padding: 0 10px;
          height: 42px;
          border-top-left-radius 8px;
          border-top-right-radius 8px;
        }
        .title {
          line-height: 42px;
          float: left
        }
        .showimg {
          text-align: center;
          padding-top: 5px;
          .chart-title {
            width: 840px;
            margin: 10px auto;
            position: relative;
            text-align left
            height 40px;
            line-height 40px;
            border-bottom 1px solid #dcdfe6;
          }
          .contentdiv {
            width: 840px;
            margin: 0 auto;
            padding: 10px 10px;
            position relative
            .answer {
              height 48px;
              width: 100%
              line-height 48px;
              text-align left
              text-overflow: ellipsis;
              border-bottom 1px solid #dcdfe6;
            }
            .pager {
              position absolute
              bottom -20px
              right 0
            }
          }
          .chartdiv {
            width: 840px;
            margin: 0 auto;
            padding: 10px 10px;
            position: relative;
            display flex
            justify-content space-between
            .charts-container {
              position: relative;
              overflow: hidden;
              width: 50%;
              height: 400px;
              text-align: left;
              line-height: normal;
              z-index: 0;
              -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
            }
            .chart_table {
              width 410px;
              margin-top:20px;
              display:block;
              overflow-x:auto;
              float: left
            }
          }
        }
      }
    }
  }
</style>



